.pageLoading {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-bg-page);
  z-index: 800;
}

.pageLoading > * {
  background-color: var(--shadow-dark);
  border-radius: var(--border-radius);
  padding: var(--margin-s)
}
