:root {
  --slidingTab-duration: 0.75s;
}

.slidingTab-overlay {
  position: fixed;
  top: var(--navbar-height);
  left: 0;
  height: calc(100vh - var(--navbar-height));
  width: 100%;
  background-color: var(--shadow-dark);
  z-index: 900;
  animation: fadeIn var(--slidingTab-duration) forwards;
}

.slidingTab {
  position: fixed;
  top: var(--navbar-height);
  height: calc(100vh - var(--navbar-height));
  width: 100%;
  max-width: var(--tab-max-width);
  background-color: var(--color-bg-page);
  z-index: 900;
  overflow-y: auto;
  overflow-x: hidden;
  transition: left var(--slidingTab-duration), right var(--slidingTab-duration);
}
